$color-blue: #008dde;
$color-dark-blue: #273647;
$color-black: #282828;

.draggerDarkH {
  background-color: #fff;
}
.draggerDarkV {
  background-color: #fff;
}

.gutterDarkH {
  background-color: $color-black;
}
.gutterDarkV {
  background-color: $color-black;
}

.draggerLightH {
  background-color: $color-black;
}
.draggerLighV {
  background-color: $color-black;
}

.gutterLightH {
  background-color: #fff;
}
.gutterLightV {
  background-color: #fff;
}

.wrap {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  background: #fff;
  overflow-y: auto;

}

.preview {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;

  .header {
    display: flex;
    flex: 0 0 75px;
    justify-content: space-between;
    padding: 20px 25px;

    .btn {
      p {
        font-size: 14px;
        padding: 1.5px;
      }
    }

    .headerLeft {
      display: flex;
      flex-direction: column;
      gap: 6px;

      @media (min-width: 768px) {
        flex-direction: row;
      }
    }

    .headerRight {
      display: flex;
      flex-direction: column;
      gap: 6px;

      @media (min-width: 768px) {
        flex-direction: row;
      }
    }
  }

  .postContainer {
    flex: 1;
    display: flex;
    justify-content: center;


    .post {
      width: 100%;
    }
  }
}
